const buttons = document.querySelectorAll('.ingredients__list-link');
const popovers = document.querySelectorAll('.ingredients__descr');
const ingrList = document.querySelector('.ingredients__list');

if (buttons.length > 0) {
  // координаты первой ссылки в слайдере для послед. корректировки позиции (т.к. на разных экранах слева разные отступы)
  let firstLink = Math.round(buttons[0].getBoundingClientRect().x);

  // размер гэпов между элементами в слайдере
  const styles = window.getComputedStyle(ingrList);
  let gap = parseInt(styles.columnGap);


  // узнаем координаты кнопки и рассчитываем left для поповера
  const popoverCoords = function (link, popover) {
    let linkCoords = link.getBoundingClientRect();
    let xLink = Math.round(linkCoords.x);
    let linkWidth = Math.round(linkCoords.width);
    let left = Math.round(linkWidth / 2 + xLink - firstLink - (gap / 2));   // 30 = 1/2 от gap
    // для мобильных экранов
    if (window.innerWidth < 834) {
      left = 45; // 45 - это отступ, который получается по формуле выше для первой ссылки, т.е. совпдает с началом слайдера
    }
    if (left < 0) {
      left = 45;
    }
    // для остальных
    popover.style.left = left + 'px';
  }

  buttons.forEach((link, index) => {
    // по наведению мыши на кнопку появляется поповер
    link.addEventListener('mouseover', (ev) => {
      popovers.forEach((item, inx) => {
        if (inx === index) {
          popoverCoords(link, item);// узнаем координаты кнопки и рассчитываем left для поповера
          item.style.display = 'block';
        }
      });
    });

    // по щелчку мыши на кнопку появляется поповер
    link.addEventListener('click', (ev) => {
      ev.preventDefault();
      popovers.forEach((item, inx) => {
        if (inx === index) {
          popoverCoords(link, item);// узнаем координаты кнопки и рассчитываем left для поповера
          item.style.display = 'block';
        } else {
          item.style.display = 'none';
        }
      });
    });

    // по отведению мыши поповер исчезает
    link.addEventListener('mouseout', () => {
      popovers.forEach((item, inx) => {
        if (inx === index) {
          item.style.display = 'none';
        }
      });
    });
  });
}
